<template>
    <div class="header">
        <nav class="site-nav">

            <div class="container">

                <h1 class="logo">
                    <a href="#">{{title}}</a>
                </h1>

                <button id="mobile-menu-toggle" type="button" @click="toggleMenu">
                    <i class="fas fa-bars"></i>
                    <span>Menu</span>
                </button>

                <ul class="nav-menu">
                    <li class="active">
                        <a href="#">Home</a>
                    </li>
                    <li>
                        <a href="#">About</a>
                    </li>
                    <li>
                        <a href="#">Services</a>
                    </li>
                    <li>
                        <a href="#">Contact</a>
                    </li>
                </ul>

            </div>

        </nav>
    </div>
</template>

<script>
    export default {
        name: "Header",
        data: function () {
            return {
                title: 'Ricardo Pestana',
                mobileMenu: document.querySelector('.nav-menu'),
                mobileMenuToggle: document.querySelector('#mobile-menu-toggle')
            }
        },
        created: function () {
            this.mobileMenuToggle.addEventListener('click', this.toggleMenu);
        },
        methods: {
            toggleMenu: function (e) {
                console.log('toggled');
                e.querySelector('.fas').classList.toggle('fa-bars');
                e.querySelector('.fas').classList.toggle('fa-times');
                this.mobileMenu.classList.toggle('active');
            }


        }
    }

</script>

